.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.spinner {
  margin: 10px auto;
  width: 30px;
  height: 30px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #4CAF50;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.GamePage-container {
  background-color: #282c34; /* Match the header background */
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  justify-content: flex-start;
  padding-top: 10vh;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow-x: hidden; /* Prevent horizontal scrolling */
  max-width: 100vw; /* Ensures content stays within screen bounds */
}

.element {
  animation: fadeInScaleUp 0.5s, spin 1s infinite;
}


@keyframes fadeInScaleUp {
  0% {
      delay: 0.5s;
      opacity: 0;
      transform: scale(4.0);
  }
  100% {
      opacity: 1;
      transform: scale(1.8);
  }
}

.game-completion-message1 {
  color: pink;
  font-size: clamp(1.5rem, 5vw, 3rem); /* Dynamically scales */
  margin-top: calc(30px + 6vmin);
  text-align: center;
  font-weight: bold;
  animation: spin 0.25s ease-out forwards;
  max-width: 90%; /* Prevents text overflow */
  white-space: normal; /* Allows multi-line wrapping */
  word-wrap: break-word;
  overflow-wrap: break-word;
  padding: 0 10px; /* Adds slight padding to avoid edge cutoff */
  display: block;
}

.game-completion-message2 {
  color: pink;
  font-size: clamp(1rem, 4vw, 2rem); /* Dynamically scales */
  margin-top: calc(15px + 4vmin);
  text-align: center;
  font-weight: bold;
  animation: fadeInScaleUp 1s ease-out forwards;
  max-width: 90%; /* Ensures the text does not exceed the screen width */
  white-space: normal; /* Allows multi-line wrapping */
  word-wrap: break-word;
  overflow-wrap: break-word;
  padding: 0 10px; /* Adds slight padding to avoid edge cutoff */
  display: block;
}


