.DraggableList-container {
  position: relative; /* Ensures lists scroll properly and don't float */
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  z-index: 1; /* Ensures it stays below other elements */
}

html, body {
  overflow-x: hidden; /* Prevents horizontal scrolling */
  width: 100%;
  max-width: 100vw; /* Ensures content does not exceed screen width */
  background: linear-gradient(90deg, #f8c7ff, #acd8ff); /* Keeps background */
  position: relative;
}
